<template>
  <div>
      <h1>Radio组件的使用</h1>
      <el-radio v-model="label" label="男">男</el-radio>
      <el-radio v-model="label" label="女">女</el-radio>
  </div>
</template>

<script>
export default {
    name:"Radio",
    data(){
        return{
            label:'男'
        }
    }
}
</script>

